@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
body {
  font-family: 'Poppins', sans-serif;
  font-weight: 300;
}

h1, h2, h3 {
  font-weight: 300;
  margin: 30px 0
}

a, a:hover {
  color: inherit;
  text-decoration: none;
}


img {
  width: 100%;
}
.button-nav {
    border-radius: 20px;
    width: 200px;
    background-color: white;
}

.logo {
    width: 100px;
}

.backHome {
    background-size: 100%;
    background-repeat: no-repeat;
    object-fit: cover;
}

